.title {
  margin: 0 40px 40px 0;
  padding: 20px;
  border: 4px solid lightblue;
  border-radius: 13px;
  font-size: 24px;
  font-weight: 700;
  transition: border-color .2s;
  cursor: pointer;
}

.active {
  composes: title;
  cursor: default;
}

.active,
.title:hover {
  border-color: red;
}